<template>
  <b-navbar toggleable="lg" class="navbar-custom" variant="dark">
    <b-container class="navbar-container">
      <!-- Contenedor del título -->
      <div class="brand-container">
        <div class="brand-box">
          <b-navbar-brand href="/">
            <i class="fas fa-gamepad"></i> Catálogo de Juegos
          </b-navbar-brand>
        </div>
      </div>

      <div class="search-container">
        <b-form inline @submit.prevent="handleSearch" class="search-form">
          <b-form-input
              v-model="searchQuery"
              placeholder="Buscar juegos..."
              class="search-input"
          ></b-form-input>
          <b-button type="submit" variant="light" class="search-button">
            <i class="fas fa-search"></i>
          </b-button>
        </b-form>
      </div>
      <b-button type="submit" variant="light" class="search-button" @click="adminPage" v-if="showLogoutButton">
        <i class="fas fa-id-card" aria-hidden="true"></i>
      </b-button>
      <b-button type="submit" variant="light" class="search-button" @click="handleLogout" v-if="showLogoutButton">
        <i class="fas fa-sign-out"></i>
      </b-button>
    </b-container>
  </b-navbar>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "AppNavbar",
  data() {
    return {
      searchQuery: "",
    };
  },
  computed: {
    ...mapGetters(["currentUser"]),
    showLogoutButton() {
      return !!this.currentUser;
    },
    ...mapGetters(["allGames"]),
  },
  methods: {
    handleLogout() {
      this.$store.dispatch("logout");
    },
    adminPage() {
      this.$router.push("/administracion");
    },
    ...mapActions(["searchGames"]), // Acción para buscar juegos en la API
    handleSearch() {
      if (this.searchQuery.trim()) {
        this.searchGames(this.searchQuery); // Llama a la API para buscar
      }
    },
  },
};
</script>

